*{
  font-family: Tahoma;
  box-sizing: border-box;
}

header{
  color: beige;
  margin-bottom: 20px;
  border-bottom: 1px solid yellow;
  padding: 0 10px;
}
body{
  background-color: #333;
  color: #fff;
}
button{
  margin: auto;
  font-size: 20px;
  border-radius: 5px;
  border: none;
  padding: 5px;
  cursor: pointer;
}

li{
  margin-bottom: 10px;
}